// middleware.ts
import { NextResponse } from "next/server";
import type { NextRequest } from "next/server";

/** Helper: dcodage base64 compatible Edge + Node (dev) */
function decodeBase64(b64: string): string {
  // atob est dispo en Edge; fallback Node pour le dev local si besoin
  // @ts-ignore
  if (typeof atob === "function") return atob(b64);
  // eslint-disable-next-line n/no-deprecated-api
  return Buffer.from(b64, "base64").toString("utf-8");
}

/**
 * Middleware dauth Basic pour /admin et /dashboard
 * - Ne bloque PAS les routes /api/*
 * - Utilise atob (Edge-safe), fallback Buffer en dev
 * - Supporte mots de passe contenant ":" (on joine le reste)
 */
export function middleware(req: NextRequest) {
  const { pathname } = req.nextUrl;

  // Laisse passer toutes les API (important pour le front)
  if (pathname.startsWith("/api/")) {
    return NextResponse.next();
  }

  // Ce bloc reprend ton ide initiale "protger uniquement l'admin"
  // mais comme le matcher inclut /dashboard (voir export config ci-dessous),
  // on protge bien /admin ET /dashboard ici.
  const expectedUser = process.env.ADMIN_USER ?? "";
  const expectedPass = process.env.ADMIN_PASS ?? "";

  // Si les identifiants ne sont pas configurs, renvoyer un 401 explicite
  if (!expectedUser || !expectedPass) {
    return new NextResponse("Admin credentials not set", {
      status: 401,
      headers: { "WWW-Authenticate": 'Basic realm="Admin Area"' },
    });
    // (Ce comportement correspond  ton premier bloc.)
  }

  // Header Authorization: "Basic base64(user:pass)"
  const auth = req.headers.get("authorization") || "";
  const [scheme, encoded] = auth.split(" ");

  if (scheme !== "Basic" || !encoded) {
    return new NextResponse("Auth required", {
      status: 401,
      headers: { "WWW-Authenticate": 'Basic realm="Admin Area"' },
    });
    // (Conforme  tes deux blocs.)
  }

  let decoded = "";
  try {
    decoded = decodeBase64(encoded);
  } catch {
    return new NextResponse("Invalid auth header", {
      status: 401,
      headers: { "WWW-Authenticate": 'Basic realm="Admin Area"' },
    });
  }

  // Le mot de passe peut contenir ":" -> on join le reste (conforme  ton 1er bloc)
  const [user, ...passParts] = decoded.split(":");
  const pass = passParts.join(":");

  // Validation
  if (user === expectedUser && pass === expectedPass) {
    return NextResponse.next();
  }

  return new NextResponse("Unauthorized", {
    status: 401,
    headers: { "WWW-Authenticate": 'Basic realm="Admin Area"' },
  });
}

/**
 * Matcher : protge /admin/* ET /dashboard/* (comme dans ton 2e bloc),
 * en vitant toute duplication d'exports.
 */
export const config = {
  matcher: ["/admin/:path*", "/dashboard/:path*"],
};